.nav_bar {
  height: 100px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  z-index: 999; }

.logo_header {
  display: block;
  height: 100%;
  width: 240px;
  background-color: #ffd10a;
  text-align: center; }
  .logo_header img {
    padding-top: 25px;
    position: relative;
    z-index: 1; }
  .logo_header::after {
    content: '';
    position: absolute;
    top: -14px;
    right: 17px;
    width: 50px;
    height: 150px;
    background-color: inherit;
    transform: rotate(15deg);
    z-index: 0; }

.logo__wrap {
  position: relative;
  width: 280px;
  height: 100%;
  overflow: hidden;
  left: 0;
  z-index: 99; }

.logo_wrap_native {
  animation-duration: 1s;
  animation-name: logosNative; }
  .logo_wrap_native .logo_header {
    transition-delay: .5s;
    background-color: #ffd10a; }

.logo_wrap_active {
  background-color: transparent;
  animation-duration: 1s;
  animation-name: logos;
  animation-delay: .4s; }
  .logo_wrap_active .logo_header {
    transition-delay: .6s;
    background-color: #2b2a27; }

@keyframes logosNative {
  0% {
    left: 0;
    width: 280px; }
  10% {
    width: 240px; }
  50% {
    left: -60%; }
  70% {
    width: 280px; }
  100% {
    left: 0; } }

.logo_wrap_native {
  animation-duration: 1s;
  animation-name: logosNative; }
  .logo_wrap_native .logo_header {
    transition-delay: .5s;
    background-color: #ffd10a; }

.scroll_menu {
  transition-delay: .2s !important; }

.logo_wrap_active {
  background-color: transparent;
  animation-duration: 1s;
  animation-name: logosMob;
  animation-delay: .3s; }
  .logo_wrap_active .logo_header {
    transition-delay: .57s;
    background-color: #2b2a27; }

@keyframes logosMob {
  0% {
    left: 0;
    width: 280px; }
  10% {
    width: 240px; }
  50% {
    left: -100%; }
  60% {
    left: -100%; }
  70% {
    background-color: #2b2a27;
    width: 280px; }
  100% {
    background-color: #2b2a27 !important;
    left: 0; } }

.nav_menu__block {
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 99; }

.nav_menu_burger__wrap {
  position: relative;
  width: 130px;
  height: 100%;
  overflow: hidden;
  background: transparent;
  display: flex;
  justify-content: flex-end; }

.nav_menu_burger {
  width: 100px;
  height: 100%;
  background-color: #ffd10a;
  position: relative;
  z-index: 9;
  transition: all .4s; }
  .nav_menu_burger::after {
    content: '';
    position: absolute;
    top: -14px;
    left: -17px;
    height: 150px;
    background-color: #ffd10a;
    z-index: 0;
    width: 150px;
    -webkit-transform: skew(-15deg);
    -moz-transform: skew(-15deg);
    -o-transform: skew(-15deg);
    transition: all .4s; }

.nav_menu_content {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  width: 100%;
  left: 0;
  transition: all .2s; }

.nav_menu_item__block:after {
  content: '';
  width: 150px;
  -webkit-transform: skew(-15deg);
  -moz-transform: skew(-15deg);
  -o-transform: skew(-15deg);
  background: #fff;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0px;
  transition: all .4s;
  z-index: 0; }

.nav_menu_item {
  margin-left: 17px;
  cursor: pointer; }
  .nav_menu_item:hover .svg_item {
    fill: #ffd10a; }

.burger_span {
  display: block;
  background: #000;
  border-radius: 2px;
  transition: .35s ease-in-out; }

#menu-toggle {
  width: 100px;
  height: 100px;
  position: relative;
  cursor: pointer;
  background: transparent;
  border-radius: 5px;
  z-index: 999; }
  #menu-toggle #hamburger {
    position: absolute;
    height: 100%;
    width: 100%; }
    #menu-toggle #hamburger .burger_span {
      width: 44px;
      height: 2px;
      position: relative;
      top: 24px;
      left: 30px;
      margin: 10px 0; }
      #menu-toggle #hamburger .burger_span:nth-child(1) {
        transition-delay: .5s; }
      #menu-toggle #hamburger .burger_span:nth-child(2) {
        transition-delay: .625s; }
      #menu-toggle #hamburger .burger_span:nth-child(3) {
        transition-delay: .75s; }
  #menu-toggle #cross {
    position: absolute;
    height: 100%;
    width: 100%;
    transform: rotate(45deg); }
    #menu-toggle #cross .burger_span:nth-child(1) {
      height: 0%;
      width: 2px;
      position: absolute;
      top: 17%;
      left: 48px;
      transition-delay: 0s; }
    #menu-toggle #cross .burger_span:nth-child(2) {
      width: 0%;
      height: 2px;
      position: absolute;
      left: 20%;
      top: 45px;
      transition-delay: .25s; }

#menu-toggle.open #hamburger .burger_span {
  width: 0%; }
  #menu-toggle.open #hamburger .burger_span:nth-child(1) {
    transition-delay: 0s; }
  #menu-toggle.open #hamburger .burger_span:nth-child(2) {
    transition-delay: .125s; }
  #menu-toggle.open #hamburger .burger_span:nth-child(3) {
    transition-delay: .25s; }

#menu-toggle.open #cross .burger_span {
  background: #fff; }
  #menu-toggle.open #cross .burger_span:nth-child(1) {
    height: 60%;
    transition-delay: .625s; }
  #menu-toggle.open #cross .burger_span:nth-child(2) {
    width: 60%;
    transition-delay: .375s; }

.scroll_menu {
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  transition: all .4s ease-in-out;
  transition-delay: .5s;
  right: -120%; }

.scroll_menu_active {
  right: 0;
  background: #2b2a27;
  z-index: 10; }

.nav_menu__block_active .nav_menu_item__block:after {
  left: 100%; }

.nav_menu__block_active .nav_menu_content {
  left: 120%; }

.nav_menu__block_active .nav_menu_burger__wrap {
  background: transparent; }

.nav_menu__block_active .nav_menu_burger::after {
  background-color: #2b2a27; }

@media (max-width: 533px) {
  .logo_wrap_native {
    animation-duration: 1s;
    animation-name: logosNativeMob; }
    .logo_wrap_native .logo_header {
      transition-delay: .5s;
      background-color: #ffd10a; }
  .bike_trailer__wrapper {
    padding-top: 5px; }
  @keyframes logosNativeMob {
    0% {
      left: 0;
      width: 280px; }
    10% {
      width: 240px; }
    50% {
      left: -60%; }
    70% {
      width: 280px; }
    100% {
      left: 0; } }
  @keyframes logosMob {
    0% {
      left: 0;
      width: 280px; }
    10% {
      width: 240px; }
    50% {
      left: -60%; }
    70% {
      background-color: #2b2a27;
      width: 280px; }
    100% {
      background-color: #2b2a27 !important;
      left: 0; } }
  #menu-toggle {
    height: 100%;
    width: 100%; }
  #menu-toggle #hamburger {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0; }
    #menu-toggle #hamburger .burger_span {
      width: 30px;
      height: 2px;
      top: 12px;
      left: 4px;
      margin: 7px 0; }
  #menu-toggle.open #cross .burger_span:nth-child(1) {
    height: 45%; }
  #menu-toggle #cross .burger_span:nth-child(1) {
    top: 35%;
    left: 19px; }
  #menu-toggle #cross .burger_span:nth-child(2) {
    left: 16%;
    top: 33px; }
  .nav_bar {
    height: 60px; }
  .logo_header {
    width: 153px; }
    .logo_header img {
      padding-top: 10px;
      width: 150px; }
    .logo_header:after {
      top: -14px;
      right: 19px; }
  .logo__wrap {
    width: 185px; }
  .nav_menu_burger {
    width: 45px; }
  .nav_menu_item__block::after {
    height: 60px; }
  .nav_menu_burger__wrap {
    width: 62px; }
  .nav_menu_item {
    margin-left: 10px; } }
