.first_section {
  width: 100%;
  height: 100vh;
  position: relative;
  z-index: 0; }
  .first_section::after {
    content: '';
    position: absolute;
    width: 70%;
    height: 200%;
    background-color: #efefef;
    z-index: -1;
    top: -55px;
    right: -43%;
    transform: rotate(15deg); }

.bike_trailer__wrapper {
  position: relative; }

.bike_trailer__block {
  padding-top: 65px; }

.bike_trailer_btn__wrap {
  display: flex;
  align-items: center;
  width: 350px;
  margin-top: 140px;
  justify-content: space-between; }

.bike_trailer_content {
  margin-top: 130px;
  max-width: 650px;
  margin-left: 95px;
  padding-bottom: 230px; }

.bike_trailer_img__block {
  position: absolute;
  top: 0;
  right: 0;
  width: 800px;
  height: auto; }

.bike_trailer_img {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.bike_progress__block {
  display: flex;
  align-items: center;
  width: 300px;
  margin-top: 50px; }

.bike_progress_arrow {
  margin-top: 30px; }

.num_name {
  margin: 0px 7px;
  text-align: center;
  width: 10%; }
  .num_name:nth-child(1) {
    margin-left: 0; }

.line_progress__block {
  width: 80%;
  position: relative;
  height: 1.5px;
  background-color: #3d3c3a; }

.line_active {
  position: absolute;
  top: 0;
  left: 0;
  width: 60%;
  height: 100%;
  background-color: #ffd10a; }

.slick-second img {
  transform: rotate(180deg); }

.slick-arrow {
  display: none !important; }

.all_class_slick {
  height: 16px; }

.nav_for_bike_trailer {
  margin-left: 95px; }

@media (max-width: 1309px) {
  .first_section {
    height: 100%; }
  .bike_trailer_img__block {
    top: 20%;
    width: 500px; }
  .bike_trailer_content {
    max-width: 450px;
    margin-top: 80px;
    padding-bottom: 130px; }
  .bike_progress__block {
    margin-top: 0; } }

@media (max-width: 920px) {
  .bike_trailer__wrapper {
    display: flex !important;
    flex-direction: column-reverse;
    padding-top: 70px; }
  .bike_trailer_content {
    max-width: unset;
    margin: 0 auto;
    width: 90%; }
  .bike_trailer_img__block {
    position: relative;
    width: 100%; }
  .nav_for_bike_trailer {
    margin-left: 0; } }

@media (max-width: 430px) {
  .first_section {
    height: 100vh; }
  .bike_trailer_btn__wrap {
    width: 100%;
    margin-top: 20px; }
  .bike_trailer_content {
    padding-bottom: 15px; }
  .bike_progress_arrow {
    margin-top: 20px;
    text-align: center;
    display: flex;
    justify-content: space-around; } }
