.btn_standart {
  display: block;
  font-size: 16px;
  letter-spacing: 2px;
  padding: 17px 30px;
  text-align: center; }
  .btn_standart_black {
    background-color: #2b2a27;
    color: #ffffff;
    border: solid 1px transparent; }
  .btn_standart_yellow {
    color: #2b2a27;
    background-color: #ffd10a;
    border: solid 1px transparent; }
  .btn_standart_transparent {
    color: #2b2a27;
    border: solid 1px #242321;
    background-color: transparent; }

@media (max-width: 430px) {
  .btn_standart {
    font-size: 14px;
    padding: 14px 15px; } }
